.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.pageButtons {
  min-width: 30px;
  margin: 0 5px 10px;
  padding: 3px 5px;
  font-size: 14px;
  text-align: center;
  background-color: #EDE6FF;
  border-color: transparent;
  border-radius: 5px;
  cursor: pointer;
}

.active {
  font-weight: bold;
}
